import { Inline } from '@ali/4ever-cangjie';
import { getAttributes, getTagName, JsonML } from '@ali/4ever-utils';
import InlineCode from '../models';
export default {
  name: 'inlineCode',
  match(node: JsonML): boolean {
    return getTagName(node) === InlineCode.TYPE;
  },
  convert(node: JsonML): Inline {
    return InlineCode.create({
      data: getAttributes(node) || {},
    });
  },
};
